
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'Bargain',
components: {
Toast,
Group,
XSwitch,
XButton
},
data(){
return{
order:"",
order_:"",
show: true,
count: '',
timer: null,
name:"获取验证码",
show1: false,
show4:false,
show3:false,
}
},
methods:{
qq:function(){
this.order=""
},
limitOrder () {

if (this.$refs.order.value.length > 11) {
this.$refs.order.value = this.$refs.order.value.slice(0, 11)
this.order = this.$refs.order.value
}
},
limitOrder_ () {

if (this.$refs.order_.value.length > 6) {
this.$refs.order_.value = this.$refs.order_.value.slice(0, 6)
this.order_ = this.$refs.order_.value
}
},
getCode(){
var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
if(this.order==''){
this.show4=true
}else if(!reg.test(this.order)){
this.show3=true
}else{
this.getCode_();
}
},
getCode_(){
this.show1=true
const TIME_COUNT = 60;
if (!this.timer) {
this.count = TIME_COUNT;
this.show = false;
this.timer = setInterval(() => {
if (this.count > 0 && this.count <= TIME_COUNT) {
this.count--;
} else {
this.show = true;
clearInterval(this.timer);
this.timer = null;
this.name="再次发送验证码"
}
}, 1000)
}

}
}
}



