

import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
Toast,
Group,
XSwitch,
XButton
},
name: "Buying_deta",
data() {
return {
q: false,
qq: [],
qqq: [],

imgLimit: 6,
isMultiple: true,
disabled: true,
dialogImageUrl: "",
uploadFileList: [],
dialogVisible: false,
fileList: [],
name:"",
tel:"",
show1:false,
show3:false,
show4:false,
name__:false,
nam_e:false,
qqq_:false,
};
},
methods: {

handleRemove: function(file, fileList) {

},

handlePictureCardPreview: function(file) {
this.dialogImageUrl = file.url;
this.dialogVisible = true;
},
beforeupload(file) {
const isJPEG = file.type === "image/jpeg";
const isBMP = file.type === "image/bmp";
const isPNG = file.type === "image/png";
const isGIF = file.type === "image/gif";
const isJPG = file.type === "image/jpg";
const isICON = file.type === "image/x-icon";
const isLt5M = file.size / 1024 / 1024 < 5;

if (!isJPEG && !isBMP && !isPNG && !isGIF && !isJPG && !isICON) {
this.$message.error("您选择的不是图片文件!");
}
if (!isLt5M) {
this.$message.error("您上传的图片文件过大，请上传5M内的图片!");
}
return isJPG || isBMP || isPNG || isGIF || isJPEG || isICON || isLt5M;
},
handleAvatarSuccess(response, file, fileList) {
console.log(file.response.data.img_url)
},
handleExceed(files, fileList) {
console.log(2)
},
imgUploadError(err, file, fileList) {
console.log(3)
},
name_(){

if (this.$refs.name.value.length > 11) {
this.$refs.name.value = this.$refs.name.value.slice(0, 11)
this.name = this.$refs.name.value
}
},
tel_(){

if (this.$refs.tel.value.length > 11) {
this.$refs.tel.value = this.$refs.tel.value.slice(0, 11)
this.tel = this.$refs.tel.value
}
},
getCode(){
var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
if(this.qq==''){
this.name__=true
}else if(this.qqq==''){
this.qqq_=true
}else if(this.name==''){
this.nam_e=true
}else if(this.tel==''){
this.show4=true
}else if(!reg.test(this.tel)){
this.show3=true
}else{
this.show1=true

}
},
}
};



