
import vuePickers from 'vue-pickers'
import {provs_data, citys_data, dists_data} from 'vue-pickers/lib/areaData'
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'Bargain',
components: {
Toast,
Group,
XSwitch,
XButton,
vuePickers
},
data(){
return{
name:"",
n_name:false,
dq:false,
xx:false,
tel:"",
show1:false,
show3:false,
show4:false,
text_:[],
biao:true,
isCopy: '',
res: null,
show: false,
columns: 3,
link: true,
pickData: {
data1: provs_data,
data2: citys_data,
data3: dists_data
},
sheng:"",
shi:"",
xian:""
}
},
methods:{

getCode(){
var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
if(this.name==''){
this.n_name=true
}else if(this.tel==''){
this.show4=true
}else if(!reg.test(this.tel)){
this.show3=true
}else if(this.sheng==''){
this.dq=true
}else if(this.text_==''){
this.xx=true
}else {
this.getCode_();
}

},
getCode_(){
this.show1=true
},
limitOrder_:function(){
if(this.text_==""){
this.biao=true
}else{
this.biao=false
}
},
close() {
this.show = false
},
confirmFn(val) {
this.show = false
this.res = val
this.pickData.default = [val.select1, val.select2, val.select3]
console.log(val.select1.text)
console.log(val.select2.text)
console.log(val.select3.text)
this.sheng=val.select1.text
this.shi=val.select2.text
this.xian=val.select3.text
},
toShow() {
this.show = true
},
name_ () {

if (this.$refs.name.value.length > 11) {
this.$refs.name.value = this.$refs.name.value.slice(0, 11)
this.name = this.$refs.name.value
}
},

tel_ () {

if (this.$refs.tel.value.length > 11) {
this.$refs.tel.value = this.$refs.tel.value.slice(0, 11)
this.tel = this.$refs.tel.value
}
},
}
}



