scrollBehavior(to,from,savedPosition){
  // 第一种：固定位置
  // return {x:0,y:100}
  // 第二种：页面中第一个按钮的位置 页面足够高时，有竖向滚动条时（多加几个router-view）才能看出效果
  // return {selector:".btn"}
  // 第三种：历史保存位置 savedPosition当且仅当history.popstate导航（通过浏览器触发前进和后退按钮的时候）才会被触发
  if(savedPosition){
  // console.log('saved')
  // console.log(savedPosition)
  return savedPosition;
  }else{
  // console.log('no')
  return {x:0,y:0}
  }
  }
    
