import vuePickers from 'vue-pickers'
import Clipboard from 'clipboard'
// import threeCopy from 'three-copy'
export default {
  name: 'Bargain',
   components: {
    vuePickers
  },
   data() {
    return {
      isCopy: '',
      show: false,
      columns: 1,
      isCopy: '',
      res_: null,
      show_: false,
      columns_: 3, // 两列
      defaultData: [
        {
          text: "",
        }
      ],
      pickData: {
        // 第一列的数据结构
        data1: [
          {
            text: "男",
          },
          {
            text: "女",
          },
        ]
      },
    //   一
       defaultData_: [ // 默认显示那个字段
        {
          text: '1970',
          value:"1970"
        },
        {
          text: '1',
          value:"1"
        },
        {
          text: '1',
          value:"1"
        }
      ],
      pickData_: {
        // 第一列数据结构
        data1: [
          {
            text: '1970',
            value:1970,
          },
          {
             text: '1',
            value:1,
          },
          {
            text: '1',
            value:1,
          }
        ],
        // 第二列数据结构
        data2: [
        
        ],
        // 第二列数据结构
        data3: [
        ]
      },
      res: null,
      xingb:"女",
      nian:"",
      yue:"",
      r:"",
    }
  },
  methods: {
    close() {
      console.log(123)
      this.show = false
    },
    confirmFn(val) {
    //   console.log(val)
      this.show = false
      this.res = val
      this.defaultData = [val.select1]
      console.log(this.defaultData["0"].text)
      this.xingb=this.defaultData["0"].text
    },
    toShow() {
      this.show = true
    },
     close_() {
      this.show_ = false
    },
    confirmFn_(val_) {
      console.log(val_)
      this.show_ = false
      this.res_ = val_
      this.pickData_.default = [val_.select1, val_.select2, val_.select3]
      // console.log(val_.select1.value)
      this.nian=val_.select1.value,
      this.yue=val_.select2.value
      this.r=val_.select3.value
    },
    toShow_() {
      this.show_ = true
      this.pickData_.data1=[{text:i}]
        var date=new Date;
        var year=date.getFullYear(); 
        for(var i=1900; i<=year; i++ ){
          (this.pickData_.data1).push({"text": i+"年","value":i})
        }
         for(var i=1; i<=12; i++ ){
          (this.pickData_.data2).push({"text": i+"月","value":i})
        }
        for(var i=1; i<=31; i++ ){
          (this.pickData_.data3).push({"text": i+"日","value":i})
        }
    }
  }
}



