// import HelloWorld from './HelloWorld'
export default {
  name: 'Buying_deta',
  data(){
    return{
        isA1:true,
        isB1:false,
        isA2:false,
        isB2:true,
        isA3:false,
        isB3:true,
        isA4:false,
        isB4:true,
    }
  },
  methods:{
      color:function(e){
          console.log(e.target.id)
          if(e.target.id==0){
            this.isA1=true
            this.isB2=true
            this.isB3=true
            this.isB4=true
            this.isA2=false
            this.isA3=false
            this.isA4=false
            this.isB1=false
          }else if(e.target.id==1){
            this.isA1=true
            this.isB2=false
            this.isB3=true
            this.isB4=true
            this.isA2=true
            this.isA3=false
            this.isA4=false
            this.isB1=true
          }else if(e.target.id==2){
            this.isA1=true
            this.isA2=false
            this.isA3=true
            this.isA4=false
            this.isB2=true
            this.isB3=false
            this.isB4=true
            this.isB1=true
          }else if(e.target.id==3){
            this.isA1=true
            this.isA2=false
            this.isA3=false
            this.isA4=true
            this.isB2=true
            this.isB3=true
            this.isB4=false
            this.isB1=true
          }
      }
  },
  comments:{
       color:function(e){
          console.log(e.target.id)
      }
  }
}



